import { CONNECT_WALLET } from "../actions/BlockchainActions"

const initialState = {
    wallet: undefined
}

const blockchainReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CONNECT_WALLET':
            return {...state, wallet: action.payload}
            break;
        default:
            return state
    }
}

export default blockchainReducer