import React, { useState } from 'react';
import '../App.css';
import { Menu, Button, Dropdown } from 'antd';
import { DownOutlined } from '@ant-design/icons';
import '../styles/Navbar.css'; // Ensure this path is correct
import { connectMM } from '../blockchain/connection';
import { connnectWallet } from '../redux/actions/BlockchainActions';
import { connect } from 'react-redux';

const menu = (
  <Menu>
    <Menu.Item key="1">Option 1</Menu.Item>
    <Menu.Item key="2">Option 2</Menu.Item>
    <Menu.Item key="3">Option 3</Menu.Item>
  </Menu>
);

const Navbar = (props) => {
  const [address, setAddress] = useState(undefined);

  const connectMetamask = async () => {
    try {
      console.log("Connecting to MetaMask...");
      const account = await connectMM(props.fnSetWalletData); // Correct usage of props
      setAddress(account)
      const blockchainData = props.blockchainData; // Assuming this holds the wallet address after connectMM is called
      if (blockchainData && blockchainData.walletAddress) {
        setAddress(blockchainData.walletAddress); // Update address state with the actual wallet address
      }
    } catch (error) {
      console.error("Error connecting to MetaMask:", error);
    }
  };

  return (
    <div className="navbar">
      <div className="navbar-left">
        <Dropdown overlay={menu} placement="bottomLeft">
          <Button>
            Menu <DownOutlined />
          </Button>
        </Dropdown>
      </div>
      <div className="navbar-right">
        {address ? (
          <Dropdown overlay={menu} placement="bottomLeft">
            <Button className="wallet-button">
              {address.substring(0, 6)}...{address.substring(address.length - 4)} <DownOutlined />
            </Button>
          </Dropdown>
        ) : (
          <Button type="primary" className="wallet-button" onClick={connectMetamask}>
            Connect
          </Button>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = state => ({
  blockchainData: state.blockchainData, // Adjust this based on your actual state structure
});

const mapDispatchToProps = dispatch => ({
  fnSetWalletData: data => dispatch(connnectWallet(data)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
