import { ethers } from "ethers"
import abi from "./abi.json"

const connectMM = async (setData) => {

    if (typeof window.ethereum !== 'undefined') {
        try {
            console.log("MetaMask detected, requesting accounts...");

            // Request account access
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            console.log("Accounts found:", accounts);

            if (accounts.length === 0) {
                console.log("No accounts found. User might have denied connection.");
                return;
            }

            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();

            const contract = new ethers.Contract("0xEeF6FfBeD004CC12F97874EDafdc00F657e1c969",abi,provider)
            const contractwithsigner = contract.connect(signer)

            console.log(contractwithsigner);
            console.log("MetaMask connected. Signer:", signer);
            setData(signer);

            return accounts[0]
        } catch (error) {
            console.error(error)
        }   
    } else {
        console.log('no crypto wallet found');
    }
    

}

export {
    connectMM
}